import styled from "@emotion/styled";
import { AppBar, Skeleton, Toolbar, Typography } from "@mui/material";
import { useEffect, useRef, useState } from "react";
import { useSnapshot } from "valtio";
import { HFBox } from "../components/theme";
import { langMessages, langState } from "../lang";
import {
  GetIndirectRelation,
  getJoinItem,
  GetMapLowerLevel,
} from "../wallet/user";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import { useLocation, useNavigate } from "react-router-dom";
import { timeF } from "../wallet/tools";
const HFAppBar = () => {
  const langSnap = useSnapshot(langState);
  const navigate = useNavigate();
  return (
    <AppBar position="fixed">
      <Toolbar>
        <ArrowBackIosNewIcon
          onClick={() => {
            navigate(-1);
          }}
        />
        <Typography variant="h6" noWrap sx={{ flexGrow: 1 }}>
          {langMessages[langSnap.locale].team}
        </Typography>
      </Toolbar>
    </AppBar>
  );
};

function Invitation() {
  const [superior, setSuperior] = useState("");
  const [lower, setLower] = useState<string[]>([]);
  useEffect(() => {
    getSuperior();
    getLower();
  }, []);
  const langSnap = useSnapshot(langState);

  const getSuperior = async () => {
    try {
      const address = await GetIndirectRelation();
      setSuperior(address);
    } catch (error) {}
  };
  const getLower = async () => {
    try {
      const address = await GetMapLowerLevel({});

      setLower(address);
    } catch (error) {}
  };

  return (
    <Layout>
      <HFAppBar />
      <HFBoxD>
        <HFTitle> {langMessages[langSnap.locale].superior}</HFTitle>
        <HFMsg>{superior}</HFMsg>
      </HFBoxD>
      {lower.length > 0 && (
        <HFBoxD>
          <HFTitle> {langMessages[langSnap.locale].lower}</HFTitle>
          {lower.map((item) => {
            return <HFMsg key={item}>{item}</HFMsg>;
          })}
        </HFBoxD>
      )}
    </Layout>
  );
}

const Layout = styled("div")`
  width: 100vw;
  min-height: 100vh;
  background-color: #f2f2f2;
  padding: 12px;
  padding-bottom: 50px;
  padding-top: 64px;
`;

const HFBoxD = styled(HFBox)`
  margin-top: 10px;
`;

const HFTitle = styled("div")`
  font-size: 16px;
  margin-bottom: 10px;
`;
const HFMsg = styled("div")`
  font-size: 12px;
  color: #999;
  line-height: 26px;
`;

const HFSpan = styled("span")`
  font-size: 12px;
  color: #0f3385;
`;

export default Invitation;
